/* CSS Variables for Theming */
:root {
    --primary: #6366f1;
    --primary-dark: #4f46e5;
    --primary-light: #818cf8;
    --secondary: #f472b6;
    --secondary-dark: #db2777;
    --accent: #10b981;
    --danger: #ef4444;
    --warning: #f59e0b;
    --info: #3b82f6;
    --dark: #0f172a;
    --darker: #020617;
    --light: #f8fafc;
    --lighter: #ffffff;
    --gray: #64748b;
    --gray-dark: #475569;
    --gray-light: #e2e8f0;
    --gray-lighter: #f1f5f9;
    --card-bg: #ffffff;
    --card-border: rgba(0, 0, 0, 0.08);
    --shadow: 0 20px 40px -20px rgba(0, 0, 0, 0.15);
    --shadow-lg: 0 40px 80px -30px rgba(0, 0, 0, 0.2);
    --radius: 16px;
    --radius-sm: 8px;
    --radius-lg: 24px;
    --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    --transition-slow: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
    --glass-bg: rgba(255, 255, 255, 0.9);
    --glass-border: rgba(255, 255, 255, 0.2);
    --modal-lg: 800px;
    --modal-xl: 1000px;
}

/* Dark Theme Variables */
body.dark-mode {
    --dark: #f8fafc;
    --darker: #f1f5f9;
    --light: #0f172a;
    --lighter: #1e293b;
    --gray: #94a3b8;
    --gray-dark: #cbd5e1;
    --gray-light: #334155;
    --gray-lighter: #1e293b;
    --card-bg: #1e293b;
    --card-border: rgba(255, 255, 255, 0.08);
    --shadow: 0 20px 40px -20px rgba(0, 0, 0, 0.4);
    --shadow-lg: 0 40px 80px -30px rgba(0, 0, 0, 0.5);
    --glass-bg: rgba(30, 41, 59, 0.85);
    --glass-border: rgba(255, 255, 255, 0.1);
}